<script>
  import ChartLib from './chart-library.min.js';
  import roulette01 from './image/data-overview/do-roulette-01.png';
  import roulette02 from './image/data-overview/do-roulette-02.png';
  import roulette03 from './image/data-overview/do-roulette-03.png';
  import sphere01 from './image/data-overview/do-sphere-01.png';
  import circle01 from './image/data-overview/do-circle-01.png';
  import { accountApi } from '@/request'
  /**
   * 数据概览中央复合组件，采用控制器管辖各部件。
   * @author Molay
   */
  export default {
    props: {
      nop: {
        type: String,
        default: ''
      }
    },
    data: function () {
      return {
        /*业务数据*/
        YWJson: [
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'dell'
            }         
        ],
        /*物联网数据*/
        WLWJson:[
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },       
        ],
        /*互联网数据*/
        HLWJson:[
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            }   
        ],
        /*数据应用*/
        SJYYJson:[
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            },
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            }   
        ],
        /*数据服务*/
        SJFWJson:[
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
            {
                name: 'hello'
            },
            {
                name: 'hello'
            },
            {
                name: 'world'
            },
            {
                name: 'dell'
            },
        ],
      };
    },
    mounted() {
      let me = this;
      this.getData();
      ////
      // 创建中央组件控制器，绑定视图中的数值填充区域。
      ////

      let viewport = me.$el;

      let app = new ChartLib.DataOverview({
        viewport: viewport,
        core: {
          x:645, //875
          y:130,
          width:660, //1000
          height:622, //viewport.clientHeight
          rings: [
            roulette01,
            roulette02,
            roulette03
          ],
          sphere: sphere01,
          itemBackground: circle01
        }
      });
      me._app = app;

      // 为核心区特殊数值内容区绑定处理器   处理数据
      app.registerFormatter('c_1', function (element, value) {
        return Math.round(value).toLocaleString().split('')
          .map(o => o === ',' ? o : ('<span>' + o + '</span>'))
          .join('\n') + '\n<small>条</small>';
      });
      app.registerFormatter('c_2', function (element, value) {
        return Math.round(value).toLocaleString().split('')
          .map(o => o === ',' ? o : ('<span>' + o + '</span>'))
          .join('\n') + '\n<small>KB</small>';
      });

      app.startRender(); //开始渲染
      app.appear(1000);  // 出现视图间隔

      // 将所有数值内容区的数值均重置为0。
      // 需要注意App.set用法有三种：
      // 1、app.set(value) 将所有数值内容区均设置为此值。
      // 2、app.set(key, value) 将指定key的数值内容区设置为value。
      // 3、app.set([{key:key1,value:value1}]) or app.set({key1:value1}) 批量设置数值内容区的值。
      app.set(0);

      viewport.style.opacity = '';

      ////
      // 填充本地静态数据，正式对接时应更换为接口。
      ////
      let autoIncrement = 0;
      let testTimer = new ChartLib.Timer(2000);
      testTimer.on('timer', e => {
        // 转换思路提供的接口数据，映射至数值内容区。
        let indexData = require('./json/getIndexData.json');
        const map = {
          //
          'SJLY-环保部': 'g1_1',
          'SJLY-省政府': 'g1_2',
          'SJLY-环保厅': 'g1_3',
          'SJLY-涉农部委': 'g1_4',
          'SJLY-市县环保局': 'g1_5',
          'SJLY-互联网': 'g1_5',
          'SJLY-公众': 'g1_6',
          'SJLY-企业': 'g1_7',
          //
          'WLWSJ-水环境': 'g2_1',
          'WLWSJ-气环境': 'g2_2',
          'WLWSJ-土壤环境': 'g2_3',
          'WLWSJ-声环境': 'g2_4',
          'WLWSJ-污染源综合环境': 'g2_5',
          'WLWSJ-核与辐射': 'g2_6',
          //
          'HLWSJ-舆情': 'g3_1',
          'HLWSJ-门户网站': 'g3_2',
          'HLWSJ-政策法规': 'g3_3',
          'HLWSJ-案例库': 'g3_4',
          //
          'SJYY-水环境分析': 'g4_1',
          'SJYY-气环境分析': 'g4_2',
          'SJYY-土环境分析': 'g4_3',
          'SJYY-污染源环境监督': 'g4_4',
          'SJYY-污染源监测': 'g4_5',
          'SJYY-环境应急': 'g4_6',
          'SJYY-公众服务': 'g4_7',
          'SJYY-企业服务': 'g4_8',
          //
          'SJFW-环保部': 'g5_1',
          'SJFW-省政府': 'g5_2',
          'SJFW-环保厅': 'g5_3',
          'SJFW-涉农部委': 'g5_4',
          'SJFW-市县环保局': 'g5_5',
          'SJFW-公众': 'g5_6',
          'SJFW-企业': 'g5_7',
          //
          'SJHJ-数据总条数': 'c_1',
          'SJHJ-数据容量': 'c_2'
        };
        let data = [];
        let coreData = [];

        indexData.data.map(d => {
          let value = Number(d.SJL) + autoIncrement;
          let okey = d.TYPE + '-' + d.NAME;
          let key = map[okey];
          if (key)
            data.push({
              key,
              value
            });
          else if (d.TYPE === 'SJHJ')
            coreData.push({
              key: d.NAME,
              value
            });
        });
        data.push({
          key: 'core',
          value: coreData
        });

        app.set(data);

        autoIncrement += Math.round(Math.random() * 20);
      });
      testTimer.start();
      me._testTimer = testTimer;
    },
    beforeDestroy: function () {
      let me = this;
      if (me._testTimer) {
        me._testTimer.reset();
        me._testTimer = null;
      }
      me._app.stopRender();
      me._app = null;
    },
    created: function(){
        
    },
    methods: {     
      getData: function(){
          accountApi.post('')
          .then(res => {
            // 业务数据
            let ywObj = {},
            ywArr = [],
            YWData=res.env.YW;
            if(YWData != [] && YWData != null) {
              YWData.map((item, i) => {
                ywObj = {
                  name: item, 
                };
                ywArr.push(ywObj);
              });
              // 业务数据
              this.YWJson=ywArr;
            }
            // 物联网数据
            let WLWObj = {},
            WLWArr = [],
            WLWData=res.env.WLW;
            if(WLWData != [] && WLWData != null) {
              WLWData.map((item, i) => {
                WLWObj = {
                  name: item, 
                };
                WLWArr.push(WLWObj);
              });
              // 物联网数据
              this.WLWJson=WLWArr;
            }
            // 互联网数据
            let HLWObj = {},
            HLWArr = [],
            HLWData=res.env.HLW;
            if(HLWData != [] && HLWData != null) {
              HLWData.map((item, i) => {
                HLWObj = {
                  name: item, 
                };
                HLWArr.push(HLWObj);
              });
              // 互联网数据
              this.HLWJson=HLWArr;
            }
            // 数据应用
            let SJYYObj = {},
            SJYYArr = [],
            SJYYData=res.env.SJYY;
            if(SJYYData != [] && SJYYData != null) {
              SJYYData.map((item, i) => {
                SJYYObj = {
                  name: item, 
                };
                SJYYArr.push(SJYYObj);
              });
              // 数据应用
              this.SJYYJson=SJYYArr;
            }
            // 数据服务
            let SJFWObj = {},
            SJFWArr = [],
            SJFWData=res.env.SJFW;
            if(SJFWData != [] && SJFWData != null) {
              SJFWData.map((item, i) => {
                SJFWObj = {
                  name: item, 
                };
                SJFWArr.push(SJFWObj);
              });
              // 数据服务
              this.SJFWJson=SJFWArr;
            }
          })
          .catch(error=>{
            //console.log('sssssss',error)
          })
      }
    }
  }

</script>

