    line-height: 50px; //62
  }

  .name01, .name02, .name03 {
    color: #FFF;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    //之前的
    //padding-top: 16px;
    //之后改的
    padding-top: 28px;
  }

  .numeric01, .numeric02, .numeric03 {
    font-weight: bold;
    font-size: 18px; //24
    text-align: center;
    padding-top: 3px;
  }

  .numeric01 {
    color: #00ff90;
  }

  .numeric02 {
    color: #00ffff;
  }

  .numeric03 {
    color: #55e1ff;
  }
</style>

<style lang="scss">
  .core-numeric01 {
    color: #00ffff;
    font-size: 22px; //42
    text-align: center;
    font-weight: bold;
    margin-top: 28px;

    span {
      display: inline-block;
      width: 25px; //38
      height: 40px; //64
      border: 1px solid #00ffff;
      border-radius: 4px;
      background-color: #0e2164;
      line-height: 40px;
    }

    small {
      font-size: 13px;  //20
    }
  }

  .core-numeric02 {
    color: #00ffb4;
    font-size: 22px; //42
    text-align: center;
    font-weight: bold;
    margin-top: 28px;

    span {
      display: inline-block;
      width: 25px; //38
      height: 40px; //64
      border: 1px solid #00ffb4;
      border-radius: 4px;
      background-color: #0e2164;
      line-height: 40px;
    }

    small {
      font-size: 13px;  //20
    }
  }
</style>



