export default function Request (o) {
  this.options = o || {}
  this.baseURL = o.baseURL || ''
}


// 不供外部使用
Request.prototype._req = function (options) {

    const mergeOptions = Object.assign({}, this.options, options)

    const requestFilterPromise = mergeOptions.filterRequest ? 
      mergeOptions.filterRequest(mergeOptions) : 
      Promise.resolve(mergeOptions)
    
    return requestFilterPromise.then(o => {

      return new Promise((resolve, reject) => {

        const xhr = new XMLHttpRequest()
        xhr.addEventListener('readystatechange', function () {
          if (xhr.readyState !== 4) return
          
          const res = JSON.parse(xhr.responseText)
          try {
            if (((xhr.status >= 200 && xhr.status < 300) || xhr.status === 304)) {

              const responseFilterPromise = o.filterResponse ? 
              o.filterResponse(res) :
              Promise.resolve(res)

              responseFilterPromise.then(resolve)
              .catch(reject)
            } else {
              reject(xhr.responseText)
            }
          } catch (e) {
            reject(e + xhr.responseText)
          }
    
    
        })

        xhr.open(o.method, this.baseURL + o.url)
        if (o.header) {
          for (let key in o.header) {
            xhr.setRequestHeader(key, o.header[key])
          }
        } 
        o.method === 'POST' ? xhr.send(o.body) : xhr.send()
      })
    })
}



Request.prototype.get = function (url, params, options) {

  options = options || {}
  if (params) {
    const paramPool = []
    for (let key in params) paramPool.push(`${key}=${params[key]}`)
    url += `?${paramPool.join('&')}`  
  }

  return this._req(Object.assign(options, {
    method: 'GET',
    url
  }))
}


Request.prototype.post = function (url, params, options) {
  options = options || {}

  let body
  if (params) {
    body = new FormData()
    for (let key in params) body.append(key, params[key])
  }

  return this._req(Object.assign(options, {
    method: 'POST',
    url,
    body
  }))
}

const jsonBodyHeader = { 'Content-Type': 'application/json' }


Request.prototype.postJSON = 
Request.prototype.postJsonString = function (url, params, options) {
  options = options || {}
  options.header = Object.assign(jsonBodyHeader, options.header)

  return this._req(Object.assign(options, {
    method: 'POST',
    url,
    body: JSON.stringify(params)
  }))
}


















