import Request from './utils/request'

const commonResponseFilter = res => res.code === 0 ? Promise.resolve(res.content) : Promise.reject(res)


const authHeader = {
  appId: '',
  token: '',
  accountNo: '',
  subAppId: '',
}

export const setAuth = o => {
  for (let key in o) authHeader[key] = o[key]
}

export const clearAuth = () => {
  for (let key in authHeader) authHeader[key] = ''
}
//window.location.host; ip 端口号  //202.106.10.250:48000
export const accountApi = new Request({
  //baseURL: 'http://202.106.10.250:48005/api/wfsm/dateEnv',
  baseURL: 'http://'+window.location.host+'/api/wfsm/dateEnv',
  filterResponse: commonResponseFilter,
  header: authHeader
})



  








